import { ReadyStateEmitter } from './ready-state-emitter';

export class ApplicationReadyState extends ReadyStateEmitter {
    
    public static compose( ...readyStateEmitters: Array<ReadyStateEmitter> ): ReadyStateEmitter {
        const emitter = new ApplicationReadyState(readyStateEmitters);
        
        
        return emitter;
    }
    
    
    constructor( private readonly readyStates: Array<ReadyStateEmitter> ) {
        super();
    }
    


}
